import React from "react"
import { Link } from "react-router-dom"
import img1 from "../img/me.jpg"
import timereport from "../img/timereport.png"
import atm from "../img/atm.jpg"
import bank from "../img/bank.jpg"
import chess from "../img/chess.jpg"
import animals from "../img/animals.jpg"
import circle from "../img/circle.png"
import db from "../img/db.jpg"
import numbers from "../img/numbers.jpg"
import gen from "../img/gen.webp"
import script1 from "../js/script.js"

function Portfolio() {
  return (
    <>
      <div className="sidebar">
        <div className="profile">
          <img src={img1} alt="Portrait" />
          <h3>
            <span id="flipCol">Simon </span>
            <span id="flipBack">Grenefalk</span>
          </h3>
          <p>Systemutvecklare (snart)</p>
        </div>
        <div className="menu">
          <Link to="/Labb2/">
            <span className="icon">
              <i className="fas fa-home"></i>
            </span>
            <span className="menutext">Start</span>
          </Link>
          <Link to="/Labb2/about">
            <span className="icon">
              <i className="fas fa-info-circle"></i>
            </span>
            <span className="menutext">Om mig</span>
          </Link>

          <Link to="/Labb2/cv">
            <span className="icon">
              <i className="fas fa-paperclip"></i>
            </span>
            <span className="menutext">CV</span>
          </Link>
          <Link to="/Labb2/portfolio" className="active">
            <span className="icon">
              <i className="fas fa-folder-open"></i>
            </span>
            <span className="menutext">Portfolio</span>
          </Link>
          <Link to="/Labb2/lorem">
            <span className="icon">
              <i className="fas fa-at"></i>
            </span>
            <span className="menutext">Lorem</span>
          </Link>
        </div>
        <footer className="footer">
          <ul className="contact">
            <li>
              <a href="https://instagram.com" target="_blank" rel="noreferrer">
                <span className="icon">
                  <i className="fab fa-instagram"></i>
                </span>
              </a>
            </li>
            <li>
              <a href="https://facebook.com" target="_blank" rel="noreferrer">
                <span className="icon">
                  <i className="fab fa-facebook-f"></i>
                </span>
              </a>
            </li>
            <li>
              <a href="https://linkedin.com" target="_blank" rel="noreferrer">
                <span className="icon">
                  <i className="fab fa-linkedin-in"></i>
                </span>
              </a>
            </li>
          </ul>
        </footer>
      </div>
      <div className="container">
        <div className="top">
          <h2>Portfolio</h2>
        </div>
        <div class="portfolio">
          <div class="content">
            <ul>
              <li>
                <a href="#popup1" class="btn">
                  <img src={timereport} title="Timereport" />
                </a>
              </li>
              <li>
                <a href="#popup2" class="btn">
                  <img src={atm} title="Bankomat" />
                </a>
              </li>
              <li>
                <a href="#popup3" class="btn">
                  <img src={circle} title="Cirkel" />
                </a>
              </li>
              <li>
                <a href="#popup4" class="btn">
                  <img src={animals} title="Animals" />
                </a>
              </li>
              <li>
                <a href="#popup5" class="btn">
                  <img src={numbers} title="NumbersGame" />
                </a>
              </li>
              <li>
                <a href="#popup6" class="btn">
                  <img src={chess} title="ChessGame" />
                </a>
              </li>
              <li>
                <a href="#popup7" class="btn">
                  <img src={db} title="DatabasProjekt" />
                </a>
              </li>
              <li>
                <a href="#popup8" class="btn">
                  <img src={bank} title="Bank" />
                </a>
              </li>
              <li>
                <a href="#popup9" class="btn">
                  <img src={gen} title="Generics" />
                </a>
              </li>
            </ul>
          </div>

          <div id="popup1" class="overlay">
            <div class="popup">
              <h2>Timereport</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>REST-API som fungerar som ett tidrapporteringssystem.</p>
                <a href="https://github.com/sigreyo/TimeReport" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup2" class="overlay">
            <div class="popup">
              <h2>Bankomat</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Konsolprogram som simulerar en bankomat.</p>
                <a href="https://github.com/sigreyo/ATM" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup3" class="overlay">
            <div class="popup">
              <h2>Cirkel</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>
                  Enklare program fr att rkna area och volym p geometriska
                  figurer.
                </p>
                <a href="https://github.com/sigreyo/Circle" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup4" class="overlay">
            <div class="popup">
              <h2>Animals</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Projekt som inbegriper factory pattern.</p>
                <a href="https://github.com/sigreyo/Animals" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup5" class="overlay">
            <div class="popup">
              <h2>NumbersGame</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Spel fr att gissa rtt siffra.</p>
                <a
                  href="https://github.com/sigreyo/NumbersGame"
                  target="_blank"
                >
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup6" class="overlay">
            <div class="popup">
              <h2>ChessGame</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>
                  Program som skriver ut ett schackbrde utifrn anvndarens
                  inmatning.
                </p>
                <a href="https://github.com/sigreyo/ChessBoard" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup7" class="overlay">
            <div class="popup">
              <h2>Databasprojekt</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Projektarbete i databaskursen.</p>
                <a
                  href="https://github.com/sigreyo/DatabasProjekt"
                  target="_blank"
                >
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup8" class="overlay">
            <div class="popup">
              <h2>Bank</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Grupparbete, simulerar en bank med anvndare och konton.</p>
                <a href="https://github.com/sigreyo/SUT21-Bank" target="_blank">
                  LNK
                </a>
              </div>
            </div>
          </div>

          <div id="popup9" class="overlay">
            <div class="popup">
              <h2>Generics</h2>
              <a class="close" href="#">
                &times;
              </a>
              <div class="info">
                <p>Labb fr att va p generics.</p>
                <a
                  href="https://github.com/sigreyo/Collection-Generics"
                  target="_blank"
                >
                  LNK
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="bottom"></div>
      </div>
      <script src={script1}></script>
    </>
  )
}

export default Portfolio
